import PropTypes from 'prop-types';
import React from 'react';
import { motion } from 'framer-motion';

import styles from './ModalSheet.scss';

export function ModalSheetPane({ header, children, motionProps, padding, style, ...rest }) {
    const Component = motionProps ? motion.div : 'div';

    return (
        <Component className={styles.sheetyPane} {...motionProps}>
            {header && <Component className={styles.header}>{header}</Component>}
            <Component className={styles.content} style={padding ? { padding: 16, ...style } : style} {...rest}>
                {children}
            </Component>
        </Component>
    );
}

ModalSheetPane.propTypes = {
    header: PropTypes.any,
    children: PropTypes.any.isRequired,
    padding: PropTypes.bool,
    motionProps: PropTypes.any,
    style: PropTypes.any,
};

ModalSheetPane.defaultProps = {
    header: null,
    padding: true,
    motionProps: null,
    style: null,
};
