import { useCallback, useEffect, useMemo, useState } from 'react';
import { useAsyncFn, useMountedState } from 'react-use';

import { useActions, useFilter } from '@mssgme/hooks';
import { useProductsContext } from './useProductsContext';

export const useProductsList = (collectionId) => {
    const { api } = useActions();
    const { specifier } = useProductsContext();
    const [filter, setFilter] = useState({ name: '' });
    const [items, setItems] = useState([]);
    const isMounted = useMountedState();
    const tidyFilter = useMemo(() => Object.entries(filter).reduce((acc, [k, v]) => (v && (acc[k] = v), acc), {}), [
        filter,
    ]);
    const filtered = useFilter(items, tidyFilter, { name: 1 });

    const [{ loading: isFetching }, load] = useAsyncFn(
        async (query) => {
            const results = await api.products.listByCollection({
                collectionId,
                query: {
                    ...specifier,
                    ...query,
                },
            });

            if (isMounted()) {
                setItems(results);
            }
        },
        [collectionId, specifier]
    );

    const clearFilter = useCallback(() => setFilter({ name: '' }), []);
    const handleFilter = useCallback(({ target: { name, value } }) => setFilter((old) => ({ ...old, [name]: value })));

    useEffect(() => {
        void load();
    }, [load]);

    return {
        collectionId,
        items: filtered,
        filter,
        tidyFilter,
        clearFilter,
        handleFilter,
        isFetching,
        load,
    };
};
