import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';

import { cls } from '@mssgme/helpers';

import styles from './FeedbackItemForm.scss';

export const DataItem = ({ label, children, spread, ...rest }) => {
    return (
        <div {...cls(styles.infoLine, rest)}>
            {label && <div className={styles.prop}>{label}</div>}
            {!!children && <div className={cn(styles.value, spread && styles.spread)}>{children}</div>}
        </div>
    );
};

DataItem.propTypes = {
    label: PropTypes.any,
    spread: PropTypes.bool,
};
