import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { Fragment, useCallback, useEffect, useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { CheckIcon, CloseCircleIcon, ClockIcon, DeleteIcon } from '@mssgme/icons';
import { format } from '@mssgme/i18n';
import { useActions, useFeatures, useFeedbackStore, useFilter, useNavigationUtils } from '@mssgme/hooks';
import { Button, LoadingIndicator, SwipeableList } from '@mssgme/ui';

import { streamItems } from './stream';
import { byTerm } from './byTerm';
import { SwipeableFeedbackItem } from './SwipableFeedbackItem';

import styles from './FeedbackItemsList.scss';

const actions = [
    { action: 'processing', icon: ClockIcon, safe: true },
    { action: 'accepted', icon: CheckIcon, safe: true },
    { action: 'rejected', icon: CloseCircleIcon },
    { action: 'delete', icon: DeleteIcon, filter: ({ features }) => features('delete_feedbacks') },
];
const filterAction = (context) => (item) => !!context.safe === !!item.safe && !(item.filter && !item.filter(context));

export function FeedbackItemsList({ websiteId }) {
    const { api } = useActions();
    const { items: all, filter: rawFilter, next, isFetching, guid } = useFeedbackStore();
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const features = useFeatures();
    const items = useFilter(all, rawFilter, { term: byTerm });
    const chunks = useMemo(() => Array.from(streamItems(items)), [items]);
    const filter = useMemo(() => Object.entries(rawFilter).reduce((acc, [k, v]) => (v && (acc[k] = v), acc), {}), [
        rawFilter,
    ]);

    const handleLoadMore = useCallback(() => {
        if (isFetching || !next) {
            return;
        }

        void api.feedbacks.list({ websiteId, query: { ...filter, next } });
    }, [websiteId, filter, next, isFetching]);

    const handleAction = useCallback(async (item, { action }) => {
        if (action === 'delete') {
            return api.feedbacks.delete({
                websiteId: item.website,
                feedbackId: item._id,
            });
        }

        return api.feedbacks.update({
            websiteId: item.website,
            feedbackId: item._id,
            payload: { status: action },
        });
    }, []);
    const makeActions = useCallback(({ item }, left) => {
        return actions.filter(filterAction({ features, safe: !!left })).map(({ action, icon }) => ({
            action,
            icon,
            title: t(`Feedback.View.List.labels.state.${action}`),
            props: { disabled: item.status === action, className: cn(styles.action, styles[action]) },
        }));
    }, []);

    useEffect(() => {
        void api.feedbacks.list({
            guid: guid + 1,
            query: {
                ...(filter && { ...filter }),
            },
            websiteId,
        });
    }, [websiteId, guid, filter]);

    return (
        <>
            <SwipeableList>
                {chunks.map(({ type, base, items }) =>
                    type === 'date' ? (
                        <li key={`${base._id}-date`} className={styles.dateSection}>
                            {format(new Date(base.timestamp), 'LL')}
                        </li>
                    ) : (
                        <Fragment key={`${base._id}-items`}>
                            {items.map((item, index) => (
                                <SwipeableFeedbackItem
                                    key={item._id || index}
                                    item={item}
                                    to={generateUrl(item._id)}
                                    as="li"
                                    actions={makeActions}
                                    onAction={handleAction}
                                />
                            ))}
                        </Fragment>
                    )
                )}
            </SwipeableList>

            {!items.length &&
                (isFetching ? (
                    <LoadingIndicator color="#2979ff" />
                ) : (
                    <div className={styles.emptyMessage}>
                        <h3>{t('Feedback.View.List.emptyMessage')}</h3>
                    </div>
                ))}

            {next && (
                <Button fullWidth style={{ marginTop: 16 }} isFetching={isFetching} onClick={handleLoadMore}>
                    {t('Feedback.View.List.labels.loadMore')}
                </Button>
            )}
        </>
    );
}

FeedbackItemsList.propTypes = {
    websiteId: PropTypes.string.isRequired,
};
