import PropTypes from 'prop-types';
import React, { useRef } from 'react';

import { cls } from '@mssgme/helpers';
import { usePageTheme, BackgroundImage } from '@mssgme/userpage-core';

import styles from './StyledBlockPreview.scss';

export function StyledBlockPreview({ children, style: baseStyle, contentStyle, ...rest }) {
    const contentRef = useRef(null);
    const { theme, style } = usePageTheme(true, true);

    return (
        <div style={{ padding: 0, ...style, ...baseStyle }} {...cls(styles.root, rest)} data-el="preview-block">
            {theme.backgroundImage && (
                <div className={styles.backgroundWrapper}>
                    <BackgroundImage path={theme.backgroundImage} opacity={theme.backgroundImageOpacity} />
                </div>
            )}

            <div ref={contentRef} className={styles.content} style={contentStyle}>
                {React.Children.map(
                    children,
                    (child) =>
                        child &&
                        React.cloneElement(child, {
                            style: {
                                ...child.props.style,
                                position: 'relative',
                            },
                        })
                )}
            </div>
        </div>
    );
}

StyledBlockPreview.propTypes = {
    children: PropTypes.any,
    style: PropTypes.object,
    contentStyle: PropTypes.object,
};
