import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { useUnmount } from 'react-use';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';
import { debounce } from 'lodash';
import { MdSearch } from 'react-icons/md';

import { useActions, useFeedbackStore, useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { Input, LandingsPropTypes, StreamlinedScreen, TabsSelector } from '@mssgme/ui';
import { printPrice } from '@mssgme/userpage-core';

import { FeedbackItemsList } from './FeedbackItemsList';

import styles from './FeedbackList.scss';

const hashValues = (o) =>
    Object.entries(o)
        .map(([key, value]) => `${key}-${value}`)
        .join(':');

export const TotalSummary = ({ totals }) => {
    return (
        <div>
            {totals.map((total) => (
                <div key={total.currency}>{printPrice(total)}</div>
            ))}
        </div>
    );
};

TotalSummary.propTypes = {
    totals: PropTypes.arrayOf(LandingsPropTypes.price).isRequired,
};

export function FeedbackList() {
    const { t } = useTranslation('landings');
    const { websiteSlug } = useParams();
    const { setFeedbackItemsFilter } = useActions();
    const { generateUrl } = useNavigationUtils();
    const { _id: websiteId } = useWebsiteBySlug(websiteSlug);
    const closePath = generateUrl('..');
    const debouncedSet = useMemo(() => debounce((f) => setFeedbackItemsFilter(f), 400), []);

    const { filter: rawFilter, counts, totals } = useFeedbackStore();
    const [filter, setFilter] = useState(rawFilter);
    const isDebounced = useRef(false);

    const handleStatus = useCallback(
        (status) => setFilter((old) => (old.status === status ? old : { ...old, status })),
        []
    );
    const handleFilter = useCallback(({ target: { name, value } }) => {
        setFilter((old) => (old[name] === value ? old : { ...old, [name]: value }));

        if (name === 'term') {
            isDebounced.current = true;
        }
    }, []);

    useEffect(() => {
        if (isDebounced.current) {
            isDebounced.current = false;
            debouncedSet(filter);

            return () => debouncedSet.cancel();
        } else {
            setFeedbackItemsFilter(filter);
        }
    }, [filter]);

    useUnmount(() => setFeedbackItemsFilter({ status: 'new' }));

    const options = useMemo(() => {
        const makeOption = (status, label) => {
            const count = counts[label || status];
            const total = totals[label || status];

            return {
                value: status,
                props: {
                    children: (
                        <div className={styles.tab}>
                            {t(`Feedback.View.List.labels.state.${label || status}`)}
                            {!!count && (
                                <div className={styles.counter}>
                                    <div>{count}</div>
                                </div>
                            )}
                            {!!total?.length && (
                                <div className={styles.counter}>
                                    <TotalSummary totals={total} />
                                </div>
                            )}
                        </div>
                    ),
                },
            };
        };

        return [
            makeOption('new'),
            makeOption('processing'),
            makeOption('accepted'),
            makeOption('rejected'),
            // makeOption(null, 'all'),
        ];
    }, [hashValues(counts), hashValues(totals)]);

    return (
        <StreamlinedScreen
            padding
            modal
            backPath={closePath}
            title={t('Feedback.View.List.heading')}
            topPanelChildren={
                <>
                    <Input
                        builderInput
                        name="term"
                        placeholder={t('ui:labels.search')}
                        icon={MdSearch}
                        autoComplete="off"
                        clearable
                        value={filter.term || ''}
                        onChange={handleFilter}
                    />

                    <TabsSelector
                        style={{ marginTop: 6 }}
                        items={options}
                        value={rawFilter.status}
                        onSelect={handleStatus}
                    />
                </>
            }
            contentStyles={{ display: 'block' }}
        >
            <StreamlinedScreen.Section style={{ minHeight: 'calc(100% - 8px)' }}>
                <FeedbackItemsList websiteId={websiteId} />
            </StreamlinedScreen.Section>
        </StreamlinedScreen>
    );
}
