import PropTypes from 'prop-types';
import React from 'react';
import { Link } from 'react-router-dom';

import { useProductsContext } from '../../hooks';
import { ProductAdminItem } from './ProductAdminItem';

import styles from './ProductsListDraggable.scss';

export function ProductsList({ products, renderMeta, onClick, noPadding, collectionId }) {
    const { router } = useProductsContext();

    if (!products.length) {
        return null;
    }

    return (
        <div className={styles.root}>
            <ul className={styles.productsList}>
                {products.map((product) => (
                    <ProductAdminItem
                        key={product._id}
                        outer="li"
                        tag={Link}
                        product={product}
                        to={router.product.toEdit(product._id, collectionId)}
                        title={product.name}
                        renderMeta={renderMeta}
                        onClick={onClick}
                        noPadding={noPadding}
                    />
                ))}
            </ul>
        </div>
    );
}

ProductsList.propTypes = {
    products: PropTypes.array.isRequired,
    renderMeta: PropTypes.func,
    onClick: PropTypes.func,
    noPadding: PropTypes.bool,
    collectionId: PropTypes.string,
};
