import PropTypes from 'prop-types';
import cn from 'classnames';
import React, { useCallback, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { FiType, FiMail, FiPhone, FiUser, FiX } from 'react-icons/fi';

import { TextField, ToggleField, PortalModalSheet, BlockListItemPreview } from '@mssgme/ui';
import { ArrayField, SubscriptionFormItemSchema } from '../../../components';

import styles from './SubscriptionFormItemsField.scss';

const ICONS = {
    name: FiUser,
    text: FiType,
    email: FiMail,
    phone: FiPhone,
};

const SubscriptionFormItem = ({ value, className, textClassName }) => {
    const Icon = ICONS[value.type];

    return (
        <div className={className}>
            <div className={cn(textClassName, styles.label)}>
                <div className={styles.icon}>
                    <Icon size={20} />
                </div>
                <span>{value.label.replace(/\n/g, ' ')}</span>
                <span>{value.required && '*'}</span>
            </div>
        </div>
    );
};

SubscriptionFormItem.propTypes = {
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};

export const SubscriptionFormItemsField = (props) => {
    const { t } = useTranslation('landings');
    const { current: labels } = useRef({
        addButton: {
            more: t('Page.Edit.SubscriptionForm.itemAddMore'),
            first: t('Page.Edit.SubscriptionForm.itemAdd'),
        },
        modal: {
            title: t('Page.Edit.SubscriptionForm.itemFormHeading'),
        },
    });
    const handleFactory = useCallback(({ setFactory, setEditingIndex, ...rest }) => {
        if (rest.type) {
            setFactory(() => ({ label: '', required: true, large: false, size: 250, ...rest }));
        } else {
            setEditingIndex(-1);
        }
    }, []);

    return (
        <ArrayField
            maxItems={10}
            labels={labels}
            Item={SubscriptionFormItem}
            validationSchema={SubscriptionFormItemSchema}
            isInitialDirty
            renderFactory={(callbacks) => (
                <PortalModalSheet
                    onClose={(template) => handleFactory({ ...template, ...callbacks })}
                    title={t('Page.Edit.SubscriptionForm.fieldTypesModal.title')}
                >
                    {({ handleClose }) => (
                        <>
                            <ul className={styles.typesList}>
                                <li
                                    onClick={() =>
                                        handleClose({
                                            type: 'name',
                                            label: t('Page.Edit.SubscriptionForm.fieldTypes.name.initialValue'),
                                        })
                                    }
                                >
                                    <BlockListItemPreview
                                        label={t('Page.Edit.SubscriptionForm.fieldTypes.name.label')}
                                        labelIcon={FiUser}
                                    />
                                </li>
                                <li
                                    onClick={() =>
                                        handleClose({
                                            type: 'phone',
                                            label: t('Page.Edit.SubscriptionForm.fieldTypes.phone.initialValue'),
                                        })
                                    }
                                >
                                    <BlockListItemPreview
                                        label={t('Page.Edit.SubscriptionForm.fieldTypes.phone.label')}
                                        labelIcon={FiPhone}
                                    />
                                </li>
                                <li
                                    onClick={() =>
                                        handleClose({
                                            type: 'email',
                                            label: t('Page.Edit.SubscriptionForm.fieldTypes.email.initialValue'),
                                        })
                                    }
                                >
                                    <BlockListItemPreview
                                        label={t('Page.Edit.SubscriptionForm.fieldTypes.email.label')}
                                        labelIcon={FiMail}
                                    />
                                </li>
                                <li onClick={() => handleClose({ type: 'text', large: true, size: 5000 })}>
                                    <BlockListItemPreview
                                        label={t('Page.Edit.SubscriptionForm.fieldTypes.text.label')}
                                        labelIcon={FiType}
                                    />
                                </li>
                            </ul>
                        </>
                    )}
                </PortalModalSheet>
            )}
            {...props}
        >
            {({ values, setFieldValue }) => (
                <div>
                    <TextField
                        builderInput
                        name="label"
                        label={t('Page.Edit.SubscriptionForm.fields.label.label')}
                        placeholder={t('Page.Edit.SubscriptionForm.fields.label.placeholder')}
                        maxLength={250}
                    />

                    <ToggleField
                        builderInput
                        name="required"
                        label={t('Page.Edit.SubscriptionForm.fields.required.label')}
                        placeholder={t('Page.Edit.SubscriptionForm.fields.required.placeholder')}
                    />
                    {values.type === 'text' && (
                        <ToggleField
                            builderInput
                            name="large"
                            value={values.size > 250}
                            label={t('Page.Edit.SubscriptionForm.fields.large.label')}
                            placeholder={t('Page.Edit.SubscriptionForm.fields.large.placeholder')}
                            onChange={(_, large) => {
                                setFieldValue('size', large ? 5000 : 250);
                            }}
                        />
                    )}
                </div>
            )}
        </ArrayField>
    );
};

SubscriptionFormItemsField.propTypes = {
    name: PropTypes.string.isRequired,
    initialEdit: PropTypes.bool,
};
