import React, { useCallback, useState } from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { useCollections, useFilter } from '@mssgme/hooks';
import { Button, Feature, FloatingActions, StreamlinedScreen, ScrollableBox } from '@mssgme/ui';
import { useProductsContext } from '../../../hooks';
import { AdminHeaderSearch, CollectionsListDraggable, FilterMessage } from '../../../UI';

export function Collections() {
    const { t } = useTranslation('products');
    const { router, specifier, isSpecific, modal } = useProductsContext();
    const { isFetching, items } = useCollections(specifier);
    const [filter, setFilter] = useState(() => ({ ...specifier, name: '' }));
    const collectionsItems = useFilter(items, filter, { name: 1 });

    const handleFilter = useCallback(({ target: { name, value } }) => {
        setFilter((old) => ({ ...old, [name]: value }));
    });

    return (
        <StreamlinedScreen
            title={t('Collections.title')}
            topPanelChildren={<AdminHeaderSearch onChange={handleFilter} value={filter.name} />}
            backPath={router.toHome()}
            modal={modal}
        >
            <Feature rule="collections:view">
                <ScrollableBox nopadding style={{ flex: 1, marginTop: 8 }}>
                    {collectionsItems.length > 0 ? (
                        <CollectionsListDraggable
                            collections={collectionsItems}
                            isFetching={isFetching}
                            draggable={false}
                        />
                    ) : filter.name ? (
                        <FilterMessage
                            title={t('labels.notFound.collections.heading')}
                            description={t('labels.notFound.collections.description')}
                            style={{ margin: 'auto' }}
                        />
                    ) : (
                        <FilterMessage
                            title={t('Collections.emptyTitle')}
                            description={t('Collections.emptyDescription')}
                            style={{ margin: 'auto' }}
                        />
                    )}
                </ScrollableBox>

                {isSpecific && (
                    <FloatingActions padded style={{ marginTop: 8 }}>
                        <Button fullWidth tag={Link} to={router.collection.toNew()}>
                            + {t('Collections.tipAddCollection')}
                        </Button>
                    </FloatingActions>
                )}
            </Feature>
        </StreamlinedScreen>
    );
}
