import React from 'react';
import PropTypes from 'prop-types';
import BasicTagsInput from 'react-tagsinput';

import styles from './TagsInput.scss';

TagsInput.propTypes = {
    id: PropTypes.string,
    placeholder: PropTypes.string,
    value: PropTypes.arrayOf(PropTypes.string).isRequired,
    onChange: PropTypes.func.isRequired,
    onBlur: PropTypes.func,
};

TagsInput.defaultProps = {
    placeholder: '+ Add tag',
};

export default function TagsInput({ id, placeholder, value, onChange, onBlur }) {
    return (
        <BasicTagsInput
            className={styles.react}
            tagProps={{ className: styles.tag, classNameRemove: styles.tagRemove }}
            inputProps={{ className: styles.input, id, placeholder, onBlur }}
            value={value}
            addOnBlur
            addOnPaste
            onlyUnique
            onChange={(tags) => onChange(tags)}
        />
    );
}
