import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Redirect } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import config from 'config';
import { useAccessControl } from '@mssgme/hooks';
import { RestrictAccessTypes } from '../../../domainTypes';
import { Button, LinkButton } from '../../Inputs';
import { PortalModalSheet } from '../../ModalSheet';

import styles from './styles.scss';

/**
 * @param {string} className
 * @param {*} [children]
 * @param {*} [referrer]
 * @param {import('@mssgme/shared').AccessRequestError} reason
 * @param {boolean} [center]
 * @param {Function} onCancel
 * @param modal
 * @param rest
 * @returns {JSX.Element}
 */
export function NoAccess({ className, children, referrer, reason, center = false, onCancel, modal, ...rest }) {
    const { t } = useTranslation('ui');
    const { isAdmin } = useAccessControl();
    const buttonTo = `${(__LEGACY__ ? config.upgrade_redirects_legacy : config.upgrade_redirects)[reason.code]}${
        reason.code === 'upgrade' ? `/${reason.target}` : ''
    }`; // TODO: do not use config
    const title = t(`NoAccess.${reason.code}.heading`, reason);
    const description =
        t(`NoAccess.${reason.code}.features.${reason.permission?.replace(':', '.')}`, '', reason) ||
        t(`NoAccess.${reason.code}.description`, reason);

    const content = (
        <div>
            <h2>{title}</h2>
            <p>{description}</p>
            {referrer && (
                <LinkButton fullWidth btnStyle="secondary2" to={referrer} style={{ marginBottom: 8 }}>
                    {t('NoAccess.cancel')}
                </LinkButton>
            )}
            {onCancel && (
                <Button fullWidth btnStyle="secondary2" onClick={onCancel} style={{ marginBottom: 8 }}>
                    {t('NoAccess.cancel')}
                </Button>
            )}
            {isAdmin && buttonTo && (
                <LinkButton
                    fullWidth
                    btnStyle="primary"
                    to={buttonTo}
                    onClick={onCancel}
                    data-button={`upgrade-to-${reason.target}`}
                    data-event="click-upgrade"
                    data-variant="restrict"
                >
                    {t(`NoAccess.${reason.code}.button`, reason)}
                </LinkButton>
            )}
            {children}
        </div>
    );

    // if (!__LEGACY__) {
    //     return <Redirect to={buttonTo} />;
    // }
    //
    if (modal) {
        return (
            <PortalModalSheet {...rest}>
                <div className={classNames(styles.root, className, { [styles.center]: center })}>{content}</div>
            </PortalModalSheet>
        );
    }

    return (
        <div className={classNames(styles.root, className, { [styles.center]: center })} {...rest}>
            {content}
        </div>
    );
}

NoAccess.propTypes = {
    className: PropTypes.string,
    reason: RestrictAccessTypes.reason.isRequired,
    onCancel: PropTypes.func,
    referrer: PropTypes.string,
    center: PropTypes.bool,
    children: PropTypes.any,
    modal: PropTypes.bool,
};
