import PropTypes from 'prop-types';
import React, { useCallback, useState } from 'react';
import { escapeRegExp } from 'lodash';
import { useTranslation } from 'react-i18next';
import { MdSearch } from 'react-icons/md';

import { useFilter } from '@mssgme/hooks';
import { CURRENCY } from '@mssgme/shared';
import { PortalModalSheet } from '../../ModalSheet';
import { FloatingActions } from '../../FloatingActions';
import ListInput from '../ListInput';
import Input from '../Input';

const bySearch = (value) => {
    const match = new RegExp(escapeRegExp(value), 'i');

    return (item) => {
        if (String(item.value).match(match)) {
            return true;
        }
        if (String(item.label).match(match)) {
            return true;
        }
        if (String(item.symbol).match(match)) {
            return true;
        }
    };
};

export const CurrencyModal = ({ renderCurrency, value, onApply }) => {
    const { t } = useTranslation('ui');
    const [filter, setFilter] = useState({ search: '' });
    const options = useFilter(CURRENCY.options, filter, { search: bySearch });

    const handleFilter = useCallback(({ target: { name, value } }) => setFilter((old) => ({ ...old, [name]: value })));

    return (
        <PortalModalSheet fullHeight={false} padding={false} title={t('CurrencyPicker.label')} onClose={onApply}>
            {({ handleClose }) => (
                <>
                    <ListInput
                        style={{ padding: '8px 16px 0 16px' }}
                        value={value}
                        options={options}
                        renderOption={renderCurrency}
                        onChange={handleClose}
                    />

                    <FloatingActions padded>
                        <Input
                            builderInput
                            name="search"
                            placeholder={t('labels.search')}
                            icon={MdSearch}
                            autoComplete="off"
                            clearable
                            value={filter.search}
                            onChange={handleFilter}
                        />
                    </FloatingActions>
                </>
            )}
        </PortalModalSheet>
    );
};

CurrencyModal.propTypes = {
    value: PropTypes.any,
    renderCurrency: PropTypes.func.isRequired,
    onApply: PropTypes.func.isRequired,
};
