import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import styles from './ScrollableBox.scss';

// TODO: remove
class ScrollableBox extends Component {
    handleScroll = ({ target }) => {
        if (this.props.onBottomScroll && target.scrollHeight - target.scrollTop < target.clientHeight + 1) {
            this.props.onBottomScroll();
        }
    };

    render() {
        const { className, nopadding, noHeader, onBottomScroll, ...rest } = this.props;
        const _className = classNames(styles.root, className, {
            [styles.noPadding]: nopadding,
            [styles.noHeader]: noHeader,
        });

        return <div className={_className} {...rest} onScroll={this.handleScroll} />;
    }
}

ScrollableBox.propTypes = {
    className: PropTypes.string,
    nopadding: PropTypes.bool,
    noHeader: PropTypes.bool,
    onBottomScroll: PropTypes.func,
};

ScrollableBox.defaultProps = {
    nopadding: false,
};

export default ScrollableBox;
