import arrayMove from 'array-move';
import PropTypes from 'prop-types';
import React, { useCallback, useState } from 'react';
import { useMount } from 'react-use';
import { useFormikContext } from 'formik';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';
import { useTranslation } from 'react-i18next';

import { cls } from '@mssgme/helpers';
import { Button, DragHandle, FieldRow, renderNode } from '@mssgme/ui';
import { ArrayItem, ArrayItemEditModal } from './ArrayItem';

import styles from './styles.scss';

const SortableItemField = SortableElement(ArrayItem);

const SortableItemsField = SortableContainer(
    ({ name, items, sortable, getItemKey, Item, onEdit, onDelete, ...rest }) => (
        <FieldRow name={name} {...cls(styles.row, rest)} builderInput>
            {items.map((item, index) => (
                <SortableItemField
                    key={getItemKey ? getItemKey(item, index) : item._id || index}
                    index={index}
                    Item={Item}
                    sortable={sortable && items.length > 1}
                    name={`${name}.${index}`}
                    onEdit={onEdit}
                    onDelete={onDelete}
                />
            ))}
        </FieldRow>
    )
);

SortableItemsField.propTypes = {
    ...SortableItemsField.propTypes,
    name: PropTypes.string.isRequired,
    items: PropTypes.array.isRequired,
    sortable: PropTypes.bool.isRequired,
    Item: PropTypes.elementType.isRequired,
    getItemKey: PropTypes.func,
    onEdit: PropTypes.func,
    onDelete: PropTypes.func,
};

SortableItemsField.defaultProps = {
    ...SortableItemsField.defaultProps,
    shouldCancelStart: DragHandle.shouldCancelStart,
    helperClass: DragHandle.helperClass,
    lockAxis: 'y',
    lockToContainerEdges: true,
    lockOffset: '-16px',
};

export const ArrayField = ({
    name,
    value,
    initialEdit,
    isInitialDirty,
    isEditable,
    isDeletable,
    sortable,
    Item,
    applyTransform,
    getItemKey,
    factory: rawFactory,
    children,
    label,
    labels,
    validationSchema,
    renderFactory,
    maxItems,
    ...rest
}) => {
    const { t } = useTranslation('ui');
    const { getFieldMeta, setFieldValue, setFieldTouched } = useFormikContext();
    const { value: raw } = getFieldMeta(name);
    const [editingIndex, setEditingIndex] = useState(initialEdit ? 0 : -1);
    const [factory, setFactory] = useState(() => rawFactory);
    const items = value || raw;
    const isEditing = isEditable && editingIndex >= 0;
    const isNew = items.length === editingIndex;

    const safeSetFactory = useCallback((factoryOrItem) => {
        setFactory(
            !factoryOrItem
                ? null
                : typeof factoryOrItem === 'function'
                    ? () => factoryOrItem
                    : () => () => factoryOrItem
        );
    }, []);

    useMount(() => {
        if (initialEdit) {
            handleAdd();
        }
    }, [initialEdit]);

    const handleAdd = useCallback(() => setEditingIndex(items.length), [items.length]);
    const handleEdit = useCallback(
        (item) => {
            if (isEditable) {
                safeSetFactory(item);
                setEditingIndex(items.indexOf(item));
            }
        },
        [items, isEditable]
    );
    const handleApply = useCallback(
        (values) => {
            setEditingIndex(-1);

            if (rawFactory) {
                safeSetFactory(rawFactory);
            } else {
                safeSetFactory(null);
            }

            if (values) {
                let transformed;

                if (applyTransform) {
                    if (false !== (transformed = applyTransform(values, editingIndex))) {
                        values = transformed || values;
                    } else {
                        return;
                    }
                }

                setFieldValue(`${name}.${editingIndex}`, values, true);
                setFieldTouched(`${name}.${editingIndex}`, true, true);
            }
        },
        [editingIndex, applyTransform, rawFactory]
    );
    const handleDeletion = useCallback(
        (item) => {
            setFieldValue(name, items.filter((i) => i !== item), true);
            setFieldTouched(name, true, true);
            setEditingIndex(-1);
        },
        [name, items]
    );

    const onSortStart = useCallback((_, e) => e && e.preventDefault && e.preventDefault(), []);
    const onSortEnd = useCallback(
        ({ oldIndex, newIndex }) => {
            if (!sortable || oldIndex === newIndex) {
                return;
            }

            setFieldValue(name, arrayMove(items, oldIndex, newIndex));
        },
        [sortable, items]
    );

    return (
        <div {...cls(styles.root, rest)}>
            <SortableItemsField
                name={name}
                label={label}
                items={items}
                sortable={sortable}
                Item={Item}
                getItemKey={getItemKey}
                onSortStart={onSortStart}
                onSortEnd={onSortEnd}
                onEdit={isEditable ? handleEdit : null}
                onDelete={isDeletable ? handleDeletion : null}
            />

            {((isEditable && !maxItems) || (isEditable && maxItems && maxItems > items.length)) && (
                <Button
                    fullWidth
                    // btnStyle="transparent"
                    tag="a"
                    // size="small"
                    className={styles.addButton}
                    disabled={isEditing}
                    onClick={handleAdd}
                >
                    +{' '}
                    {labels.addButton
                        ? typeof labels.addButton === 'string'
                            ? labels.addButton
                            : items.length >= 1
                                ? labels.addButton.more
                                : labels.addButton.first
                        : t('labels.add')}
                </Button>
            )}

            {isEditing && !factory && renderNode(renderFactory, { setFactory: safeSetFactory, setEditingIndex })}

            {isEditing && factory && (
                <ArrayItemEditModal
                    fullWidth
                    title={labels.modal?.title || t('labels.add')}
                    applyLabel={labels.modal?.apply}
                    isNew={isNew}
                    isInitialDirty={isInitialDirty}
                    name={`${name}.${editingIndex}`}
                    factory={factory}
                    validationSchema={validationSchema}
                    onApply={handleApply}
                    onDelete={isDeletable ? handleDeletion : null}
                >
                    {children}
                </ArrayItemEditModal>
            )}
        </div>
    );
};

ArrayField.propTypes = {
    name: PropTypes.string.isRequired,
    value: PropTypes.array,
    label: PropTypes.string,
    labels: PropTypes.shape({
        addButton: PropTypes.oneOfType([
            PropTypes.string,
            PropTypes.shape({ first: PropTypes.string.isRequired, more: PropTypes.string.isRequired }),
        ]),
        modal: PropTypes.shape({
            title: PropTypes.any.isRequired,
            apply: PropTypes.shape({ create: PropTypes.string.isRequired, save: PropTypes.string.isRequired }),
        }),
    }),
    sortable: PropTypes.bool,
    initialEdit: PropTypes.bool,
    isInitialDirty: PropTypes.bool,
    isEditable: PropTypes.bool,
    isDeletable: PropTypes.bool,
    Item: PropTypes.elementType.isRequired,
    children: PropTypes.any,
    getItemKey: PropTypes.func,
    factory: PropTypes.func,
    applyTransform: PropTypes.func,
    validationSchema: PropTypes.object,
    renderFactory: PropTypes.func,
    maxItems: PropTypes.number,
};

ArrayField.defaultProps = {
    value: null,
    sortable: true,
    initialEdit: false,
    labels: {},
    isEditable: true,
    isDeletable: true,
    isInitialDirty: true,
    applyTransform: null,
    validationSchema: null,
    children: null,
    getItemKey: null,
    factory: null,
    maxItems: null,
};
