import { useCallback, useEffect } from 'react';
import { $createParagraphNode, $getSelection, $isRangeSelection, COMMAND_PRIORITY_CRITICAL } from 'lexical';
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext';
import { mergeRegister } from '@lexical/utils';
import { $patchStyleText, $setBlocksType } from '@lexical/selection';
import { $createHeadingNode, $createQuoteNode } from '@lexical/rich-text';

import {
    HEADING_CONVERSION_COMMAND,
    PARAGRAPH_CONVERSION_COMMAND,
    QUOTE_CONVERSION_COMMAND,
    STYLE_FORMAT_COMMAND,
} from './constants';

export const CustomCommandPlugin = () => {
    const [editor] = useLexicalComposerContext();
    const registerCommand = useCallback(
        (command, handler) => editor.registerCommand(
            command,
            (payload) => {
                editor.update(() => {
                    const selection = $getSelection();

                    if ($isRangeSelection(selection)) {
                        handler(selection, payload);
                    }
                });

                return true;
            },
            COMMAND_PRIORITY_CRITICAL
        ),
        [editor]
    );

    useEffect(() => mergeRegister(
        registerCommand(
            STYLE_FORMAT_COMMAND,
            (selection, payload) => $patchStyleText(selection, payload)
        ),
        registerCommand(
            PARAGRAPH_CONVERSION_COMMAND,
            (selection) => $setBlocksType(selection, () => $createParagraphNode())
        ),
        registerCommand(
            QUOTE_CONVERSION_COMMAND,
            (selection) => $setBlocksType(selection, () => $createQuoteNode())
        ),
        registerCommand(
            HEADING_CONVERSION_COMMAND,
            (selection, payload) => $setBlocksType(selection, () => $createHeadingNode(payload))
        )
    ), [registerCommand]);

    return null;
};
