const extractDays = (timestamp) => ~~(timestamp / (60000 * 60 * 24));

const createChunk = (item, type = 'items') => {
    return {
        type,
        base: item,
        items: [item],
        days: extractDays(item.timestamp),
    };
};

const isDifferentGroup = (stats, item) => {
    if (extractDays(item.timestamp) !== stats.days) {
        return 'date';
    }
};

export const streamItems = function*(items) {
    let pos = 0;
    let chunk;
    let item;

    if (items[pos]) {
        yield createChunk(items[pos], 'date');
    }

    while ((item = items[pos++])) {
        if (chunk) {
            const differ = isDifferentGroup(chunk, item);

            if (differ) {
                yield chunk;

                if (differ === 'date') {
                    yield createChunk(item, 'date');
                }

                chunk = undefined;
            }
        }

        if (chunk) {
            chunk.items.push(item);
        } else {
            chunk = createChunk(item);
        }
    }

    if (chunk) {
        yield chunk;
    }
};
