export const byTerm = (value) => {
    const match = value.toLowerCase();

    if (match.match(/^#\d*$/)) {
        const order = +match.slice(1);

        return (item) => !(order && item.order !== order);
    }

    return (item) => {
        for (const { value } of item.data.values()) {
            if (
                String(value)
                    .toLowerCase()
                    .includes(match)
            ) {
                return true;
            }
        }

        if (
            item.notes &&
            String(item.notes)
                .toLowerCase()
                .includes(match)
        ) {
            return true;
        }
    };
};
