import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useFormikContext } from 'formik';
import { noop } from 'lodash';

import { isSameArray } from '@mssgme/helpers';
import { Button, FieldRow, useModal, ScrollableBox } from '@mssgme/ui';

import { useProductsList } from '../../../hooks';
import { ProductsList } from '../../ProductsListDraggable';
import { ProductsSelector } from '../../ModalSelectors';

import styles from './styles.scss';

export const ProductsField = ({ collectionId, label, toggleLabel, ...props }) => {
    const { isOpen, toggle } = useModal();
    const { getFieldMeta, setFieldValue } = useFormikContext();
    const { value = [] } = getFieldMeta(props.name);
    const products = useProductsList(collectionId);
    const mapped = useMemo(() => products.items.filter(({ _id }) => value.includes(_id)).slice(0, 500), [
        value,
        products.items,
    ]);

    const handleToggle = useCallback(() => {
        if (props.disabled) {
            return;
        }

        toggle();
    }, [props.disabled, toggle]);
    const handleDone = useCallback(
        ({ action, value: changed }) => {
            if (action === 'success' && !isSameArray(value, changed)) {
                setFieldValue(props.name, changed);
            }

            toggle(false);
        },
        [value]
    );

    const labelNode = toggleLabel ? (
        <>
            <div style={{ flex: 1, marginLeft: 16 }}>
                {label} ({value.length})
            </div>
            <Button
                tag="a"
                noborder
                nopadding
                btnStyle="transparent"
                disabled={props.disabled}
                onClick={handleToggle}
                style={{ marginRight: 16 }}
            >
                {toggleLabel}
            </Button>
        </>
    ) : (
        label
    );

    return (
        <FieldRow
            label={labelNode}
            {...props}
            style={{ display: 'grid', gridTemplateRows: 'auto auto 1fr', gridTemplateColumns: '1fr', height: '100%' }}
        >
            <div className={styles.separator} />
            <ScrollableBox nopadding style={{ minHeight: 0, background: '#fff' }}>
                <div className={cn(styles.root, props.disabled && styles.disabled)} onClick={handleToggle}>
                    <ProductsList products={mapped} onClick={noop} />
                </div>
            </ScrollableBox>

            {isOpen && <ProductsSelector products={products} value={value} onDone={handleDone} />}
        </FieldRow>
    );
};

ProductsField.propTypes = {
    ...FieldRow.propTypes,
    name: PropTypes.string.isRequired,
};
